import radioBlock from '../../components/my-components/editBlock-RadioBlock.vue'
let DataFrom = {};
let cacheVal = '';
let cacheFrom = '';
let cache = ' ';
export default {
    components: {
        radioBlock,
    },
    data() {
        return {
            defaultIndex: 0,
            list: [{
                label: '实体商品',
                value: ' ',
                subValue: '',
                id: 1
            }, {
                label: '虚拟商品',
                value: ' ',
                subValue: '',
                id: 2
            }, {
                label: '电子卡密',
                value: ' ',
                subValue: '',
                id: 3
            }]
        }
    },
    methods: {
        initPage() {
            this.list.forEach((item, index) => {
                if (item.label == DataFrom.needChange.value) {
                    this.defaultIndex = index;
                    DataFrom.needChange = {
                        label: '商品类型',
                        value: item.label,
                        from: 'editType',
                        id: this.list[index].id,
                        other: {
                            label: '商品类型',
                            value: item.label,
                            id: item.id
                        }
                    }
                    this.Cacher.setData('editType', DataFrom)
                }
            })
        },
        change(val) {
            cacheVal = val;
            console.log(val)
            this.Cacher.setData('editType', {
                needChange: {
                    label: '商品类型',
                    value: val.label,
                    other: val,
                    id: val.id
                }
            })
            cache = val.value;
        }
    },
    onLoad(option) {
        DataFrom = this.Cacher.getData(option.from) || {};
        console.log(DataFrom)
    },
    onShow() {
        this.initPage()
    }
}
